import React, { FC, useEffect, useState } from 'react'
import { CssBaseline } from '@material-ui/core'
import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive'
import cs from './app.module.scss'
import Form from './components/Form'
import { createForm, FormComponentProps } from 'rc-form'
import Partner from './components/Partner'
import Button from '@material-ui/core/Button'
import Loading from './components/Loading'

const App: FC<FormComponentProps> = ({ form }) => {
    const [partners, setPartners] = useState<number[]>([])
    const { validateFields } = form
    const [loading, setLoading] = useState(false)
    const [address, setAddress] = useState<{ id: number; label: string }[]>([])
    const handleAdd = () => {
        setPartners(prev => prev.concat(prev[prev.length - 1] + 1 || 0))
    }
    useEffect(() => {
        setTimeout(() => {
            setAddress([
                {
                    id: 12,
                    label: '呵呵'
                }
            ])
        }, 2000)
    }, [])
    const handleRemove = (index: number) => {
        const ind = partners.findIndex(i => i === index)
        const temp = partners.slice()
        temp.splice(ind, 1)
        setPartners(temp)
    }
    const handleSubmit = () => {
        validateFields((error, payload) => {
            if (!error) {
                const keys = Object.keys(payload)
                let partners: any[] = []
                keys.forEach(item => {
                    if (item.indexOf('name:') > -1) {
                        const index = +item.split(':')[1]
                        partners.push({
                            name: payload[item],
                            phone: payload[`phone:${index}`]
                        })
                    }
                })
                for (let key in payload) {
                    if (key.indexOf(':') > -1) {
                        delete payload[key]
                    }
                }
                payload.partners = partners
                setLoading(true)
                console.log(payload)
            }
        })
    }
    return (
        <>
            <Loading show={loading} />
            <CssBaseline />
            <header className={cs.header}>
                <h1>新密市内疫情出入登记表</h1>
                <div className={cs.tip}>
                    <NotificationsActiveIcon color={'secondary'} />
                    <span className={cs.info}>
                        应疫情防控要求，请新密市内出行人员进行填报登记，个人信息不会对外泄露，请如实填写。
                    </span>
                </div>
            </header>
            <main className={cs.main}>
                <Form title={'基本信息'} form={form} address={address} />
                {partners.map(item => (
                    <Partner
                        form={form}
                        key={item}
                        index={item}
                        onDelete={handleRemove}
                    />
                ))}
                <Button
                    variant={'contained'}
                    color={'primary'}
                    fullWidth
                    onClick={handleAdd}
                    style={{ marginBottom: 4 }}
                    size={"large"}
                >
                    新增同行人员
                </Button>
                <Button
                    variant={'contained'}
                    color={'secondary'}
                    fullWidth
                    size={"large"}
                    onClick={handleSubmit}
                >
                    提交
                </Button>
            </main>
        </>
    )
}

export default createForm()(App)
