import React, { FC, useEffect, useState } from 'react'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import { WrappedFormUtils } from 'rc-form'
import { TextField } from '@material-ui/core'
import FormControl from '@material-ui/core/FormControl'
import RadioGroup from '@material-ui/core/RadioGroup'
import Radio from '@material-ui/core/Radio'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import cs from './index.module.scss'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import InputLabel from '@material-ui/core/InputLabel'
import FormLabel from '@material-ui/core/FormLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import qs from 'qs'

export interface FormProps {
    title: string
    form: WrappedFormUtils
    address: { id: number; label: string }[]
}
const Form: FC<FormProps> = ({ title, form, address }) => {
    const {
        getFieldDecorator,
        getFieldValue,
        getFieldError,
        resetFields
    } = form
    const [current, setCurrent] = useState('')
    useEffect(() => {
        const search = qs.parse(window.location.search.slice(1))
        setCurrent(search.get)
    }, [])
    const renderCardRules = () => {
        if (getFieldValue('card') === '0') {
            return [
                {
                    required: true,
                    message: '请输入您的身份证'
                },

                {
                    pattern: /^[1-9]\d{5}(18|19|20|(3\d))\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
                    message: '请输入合法的身份证'
                }
            ]
        }
        return [
            {
                required: true,
                message: '请输入您的护照/回乡证/台胞证号码'
            }
        ]
    }
    return (
        <Card style={{ marginBottom: 10 }}>
            <CardHeader title={title || '基本信息'} />
            <CardContent>
                <FormControl
                  fullWidth
                  size={'small'}
                  error={!!getFieldError(`type`)}
                >
                    {getFieldDecorator('type', {
                        initialValue: '',
                        trigger: 'onChange',
                        rules: [
                            {
                                required: true,
                                message: '请选择出行状态'
                            }
                        ],
                        validateFirst: true
                    })(
                      <RadioGroup row>
                          <FormControlLabel
                            value="0"
                            control={
                                <Radio size={'small'} color="primary" />
                            }
                            classes={{
                                labelPlacementStart: cs.label
                            }}
                            label="进入"
                            labelPlacement="start"
                          />
                          <FormControlLabel
                            value="1"
                            control={
                                <Radio size={'small'} color="primary" />
                            }
                            label="外出"
                            labelPlacement="start"
                          />
                      </RadioGroup>
                    )}
                    <FormHelperText>{getFieldError(`type`)}</FormHelperText>
                </FormControl>
                {getFieldDecorator('name', {
                    initialValue: '',
                    rules: [
                        {
                            required: true,
                            message: '请输入您的姓名'
                        },
                        {
                            validator(rule, value: string, callback) {
                                if (value.length <= 8) {
                                    return callback()
                                }
                                callback('姓名不得超过8个字符')
                            }
                        }
                    ],
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        error={!!getFieldError('name')}
                        helperText={getFieldError('name')}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label="请填写您的姓名（必填）"
                    />
                )}

                <FormControl fullWidth size={'small'} >
                    {getFieldDecorator('card', {
                        initialValue: '0',
                        trigger: 'onChange'
                    })(
                        <RadioGroup
                            name="position"
                            row
                            onChange={() => {
                                resetFields(['card_detail'])
                            }}
                        >
                            <FormControlLabel
                                value="0"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                classes={{
                                    labelPlacementStart: cs.label
                                }}
                                label="身份证"
                                labelPlacement="start"
                            />
                            <FormControlLabel
                                value="1"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                label="其他"
                                labelPlacement="start"
                            />
                        </RadioGroup>
                    )}
                </FormControl>
                {getFieldDecorator('card_detail', {
                    initialValue: '',
                    rules: renderCardRules(),
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        error={!!getFieldError('card_detail')}
                        helperText={getFieldError('card_detail')}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label={
                            getFieldValue('card') === '0'
                                ? '填写18位身份证（必填）'
                                : '填写护照/回乡证/台胞证号码（必填）'
                        }
                    />
                )}

                <FormControl
                    size={'small'}
                    fullWidth
                    error={!!getFieldError('address')}
                >
                    <InputLabel
                        id="address"
                        variant={'outlined'}
                        style={{ backgroundColor: '#fff' }}
                    >
                        所属地区（必填）
                    </InputLabel>
                    {getFieldDecorator('address', {
                        initialValue: current || '',
                        rules: [
                            {
                                required: true,
                                message: '请选择你当前的所属地区'
                            }
                        ],
                        validateTrigger: 'onChange'
                    })(
                        <Select labelId="address" variant={'outlined'}>
                            {address.map(item => (
                                <MenuItem value={'' + item.id} key={item.id}>
                                    {item.label}
                                </MenuItem>
                            ))}
                        </Select>
                    )}
                    <FormHelperText>
                        {getFieldError('address')}
                    </FormHelperText>
                </FormControl>
                {getFieldDecorator('unit', {
                    initialValue: '',
                    rules: [
                        {
                            required: true,
                            message: '请输入单元号及门牌号'
                        }
                    ],
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        error={!!getFieldError('unit')}
                        helperText={getFieldError('unit')}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label={'单元号及门牌号（必填）'}
                    />
                )}
                {getFieldDecorator('phone', {
                    initialValue: '',
                    rules: [
                        {
                            required: true,
                            message: '请输入手机号'
                        },
                        {
                            pattern: /^1[0123456789]\d{9}$/,
                            message: '请输入正确格式的手机号'
                        }
                    ],
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        type={'tel'}
                        error={!!getFieldError('phone')}
                        helperText={getFieldError('phone')}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label={'手机号（必填）'}
                    />
                )}
                <FormControl
                    size={'small'}
                    fullWidth
                    error={!!getFieldError('transport')}
                >
                    <InputLabel
                        id="transport"
                        variant={'outlined'}
                        style={{ backgroundColor: '#fff' }}
                    >
                        出行方式（必填）
                    </InputLabel>
                    {getFieldDecorator('transport', {
                        initialValue: '',
                        rules: [
                            {
                                required: true,
                                message: '请选择出行方式'
                            }
                        ],
                        validateTrigger: 'onChange'
                    })(
                        <Select labelId="transport" variant={'outlined'}>
                            <MenuItem value="0">汽车</MenuItem>
                            <MenuItem value="1">其他</MenuItem>
                        </Select>
                    )}
                    <FormHelperText>
                        {getFieldError('transport')}
                    </FormHelperText>
                </FormControl>
                {getFieldValue('transport') === '0' &&
                    getFieldDecorator('transport_detail', {
                        initialValue: '',
                        rules: [
                            {
                                required: true,
                                message: '请输入车牌号'
                            }
                        ],
                        validateTrigger: 'onBlur'
                    })(
                        <TextField
                            error={!!getFieldError('transport_detail')}
                            helperText={getFieldError('transport_detail')}
                            variant={'outlined'}
                            size={'small'}
                            fullWidth
                            label={'请输入车牌号（必填）'}
                        />
                    )}
                <FormControl
                    size={'small'}
                    fullWidth
                    error={!!getFieldError('reason')}
                >
                    <InputLabel
                        id="reason"
                        variant={'outlined'}
                        style={{ backgroundColor: '#fff' }}
                    >
                        外出事由（必填）
                    </InputLabel>
                    {getFieldDecorator('reason', {
                        initialValue: '',
                        rules: [
                            {
                                required: true,
                                message: '请选择外出事由'
                            }
                        ],
                        validateTrigger: 'onChange'
                    })(
                        <Select labelId="reason" variant={'outlined'}>
                            <MenuItem value="0">采购</MenuItem>
                            <MenuItem value="1">工作</MenuItem>
                            <MenuItem value="2">其他</MenuItem>
                        </Select>
                    )}
                    <FormHelperText>{getFieldError('reason')}</FormHelperText>
                </FormControl>
                {getFieldValue('reason') === '2' &&
                    getFieldDecorator('reason_detail', {
                        initialValue: ''
                    })(
                        <TextField
                            error={!!getFieldError('reason_detail')}
                            helperText={getFieldError('reason_detail')}
                            variant={'outlined'}
                            size={'small'}
                            fullWidth
                            label={'请输入具体事由'}
                        />
                    )}
                <FormControl
                    fullWidth
                    size={'small'}
                    error={!!getFieldError(`status`)}
                >
                    <FormLabel component="legend">体温是否正常</FormLabel>
                    {getFieldDecorator('status', {
                        initialValue: '',
                        trigger: 'onChange',
                        rules: [
                            {
                                required: true,
                                message: '请选择体温状态'
                            },
                            {
                                validator(rule, value, callback) {
                                    if (value === '0') {
                                        return callback()
                                    }
                                    callback('体温异常，无法出行')
                                }
                            }
                        ],
                        validateFirst: true
                    })(
                        <RadioGroup row>
                            <FormControlLabel
                                value="0"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                classes={{
                                    labelPlacementStart: cs.label
                                }}
                                label="正常"
                                labelPlacement="start"
                            />
                            <FormControlLabel
                                value="1"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                label="异常"
                                labelPlacement="start"
                            />
                        </RadioGroup>
                    )}
                    <FormHelperText>{getFieldError(`status`)}</FormHelperText>
                </FormControl>
            </CardContent>
        </Card>
    )
}

export default Form
