import React, { FC } from 'react'
import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import IconButton from '@material-ui/core/IconButton'
import DeleteIcon from '@material-ui/icons/Delete'
import { WrappedFormUtils } from 'rc-form'
import { TextField } from '@material-ui/core'
import FormControl from '@material-ui/core/FormControl'
import RadioGroup from '@material-ui/core/RadioGroup'
import Radio from '@material-ui/core/Radio'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import cs from './index.module.scss'
import FormLabel from '@material-ui/core/FormLabel'
import FormHelperText from '@material-ui/core/FormHelperText'

export interface FormProps {
    title?: string
    form: WrappedFormUtils
    index: number
    onDelete(index: number): any
}
const Partner: FC<FormProps> = ({ title, form, index, onDelete }) => {
    const { getFieldDecorator, getFieldError } = form
    return (
        <Card style={{ marginBottom: 10 }}>
            <CardHeader
                title={title || '同行人员'}
                action={
                    <IconButton size={'small'} onClick={() => onDelete(index)}>
                        <DeleteIcon fontSize={'small'} />
                    </IconButton>
                }
            />
            <CardContent>
                {getFieldDecorator(`name:${index}`, {
                    initialValue: '',
                    rules: [
                        {
                            required: true,
                            message: '请输入您的姓名'
                        },
                        {
                            validator(rule, value: string, callback) {
                                if (value.length <= 8) {
                                    return callback()
                                }
                                callback('姓名不得超过8个字符')
                            }
                        }
                    ],
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        error={!!getFieldError(`name:${index}`)}
                        helperText={getFieldError(`name:${index}`)}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label="请填写您的姓名（必填）"
                    />
                )}
                {getFieldDecorator(`phone:${index}`, {
                    initialValue: '',
                    rules: [
                        {
                            required: true,
                            message: '请输入手机号'
                        },
                        {
                            pattern: /^1[0123456789]\d{9}$/,
                            message: '请输入正确格式的手机号'
                        }
                    ],
                    validateTrigger: 'onBlur'
                })(
                    <TextField
                        type={'tel'}
                        error={!!getFieldError(`phone:${index}`)}
                        helperText={getFieldError(`phone:${index}`)}
                        variant={'outlined'}
                        size={'small'}
                        fullWidth
                        label={'手机号（必填）'}
                    />
                )}
                <FormControl
                    fullWidth
                    size={'small'}
                    error={!!getFieldError(`status:${index}`)}
                >
                    <FormLabel component="legend">体温是否正常</FormLabel>
                    {getFieldDecorator(`status:${index}`, {
                        initialValue: '',
                        trigger: 'onChange',
                        rules: [
                            {
                                required: true,
                                message: '请选择体温状态'
                            },
                            {
                                validator(rule, value, callback) {
                                    if (value === '0') {
                                        return callback()
                                    }
                                    callback('体温异常，无法出行')
                                }
                            }
                        ]
                    })(
                        <RadioGroup row>
                            <FormControlLabel
                                value="0"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                classes={{
                                    labelPlacementStart: cs.label
                                }}
                                label="正常"
                                labelPlacement="start"
                            />
                            <FormControlLabel
                                value="1"
                                control={
                                    <Radio size={'small'} color="primary" />
                                }
                                label="异常"
                                labelPlacement="start"
                            />
                        </RadioGroup>
                    )}
                    <FormHelperText>
                        {getFieldError(`status:${index}`)}
                    </FormHelperText>
                </FormControl>
            </CardContent>
        </Card>
    )
}

export default Partner
